.row {
  display: -ms-flexbox;
  display: flex;
}

.col {
  -ms-flex: 40% 1;
      flex: 40% 1;
}

.top__margin {
  margin-top: auto;
}

.img__container__about {
  width: 85%;
  height: 100%;
  max-height: 700px;
  overflow: hidden;
}

.container {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: left;
      align-items: left;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
}

.resume__container {
  width: 60%;
}

.button {
  width: 500px;
  height: 500px;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  margin-left: 0px;
  padding: 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  background: -webkit-linear-gradient(315deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  background: -o-linear-gradient(315deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  background: linear-gradient(135deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  color: white;
}

.button:hover {
  cursor: pointer;
}

.button:after {
  right: 0;
  left: auto;
}

img {
  max-height: 100%;
}

@media only screen and (max-width: 900px) {
  .row {
    display: block;
    height: auto;
    margin-bottom: 5%;
  }

  .col {
    margin-top: 3%;
    -ms-flex: 80% 1;
        flex: 80% 1;
  }
}



