.row {
  display: -ms-flexbox;
  display: flex;
  height: auto;
  margin-bottom: 5%;
}

.col-2 {
  -ms-flex: 50% 1;
      flex: 50% 1;
}

.col-3 {
  -ms-flex: 30% 1;
      flex: 30% 1;
  margin-right: 5%;
}

.blog__container {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: left;
      align-items: left;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
}

.caption {
  font-size: .5em;
  font-weight: 100;
  font-variant: italic;
}

.img__container {
  max-width: 85%;
  max-height: 350px;
  overflow: hidden;
}

img {
  max-width: 1000px;
}

.small__img__container {
  max-width: 100%;
  max-height: 200px;
  overflow: hidden;
  margin-top: 5%;
}

@media only screen and (max-width: 800px) {

  .row {
    display: block;
    height: auto;
    margin-bottom: 5%;
  }

  .col-3 {
    -ms-flex: 80% 1;
        flex: 80% 1;
  }

  .col-2 {
    -ms-flex: 80% 1;
        flex: 80% 1;
  }

  .small__img__container {
    max-width: 100%;
    max-height: 400px;
    overflow: hidden;
    margin-top: 5%;
  }
}



