.landing__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: left;
      align-items: left;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 25% auto;
}

.button {
  max-width:100px;
  max-height: 70px;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  margin: 2%;
  padding: 5%;
  border: 2px solid #f7f7f7;
  text-align: center;
  position: relative;
  overflow:hidden;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  background: -webkit-linear-gradient(315deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  background: -o-linear-gradient(315deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  background: linear-gradient(135deg, rgba(85,239,203,1) 0%,rgba(30,87,153,1) 0%,rgba(85,239,203,1) 0%,rgba(91,202,255,1) 100%);
  color: white;
  font-weight: 700;
}

.button:hover {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .landing__container {
    margin-top: 50%;
  }
}



